.nav
{
    display: flex;
    padding: 20px;
    background-color:  white;
    border-bottom: 1px solid rgb(225, 225, 225);
    position:sticky;
}

nav a
{
    color: black;
    text-decoration: none;
    margin: 0 20px;
    padding: 10px 20px;
    transition: color 0.3s ease; 
    right: 2000px;
}
.home.active,.about.active,
nav a:hover
{
    color: lightblue;
    border-bottom: solid lightblue 5px;
}
nav > .Logo:first-child {
    margin-right: 40vw;
}
